}
.m-search-venues--venue-lists .services--cocktails {
  background: url("./assets/icons/venue/services/white/cocktails.png") no-repeat;
}
.m-search-venues--venue-lists .services--breakfast {
  background: url("./assets/icons/venue/services/white/breakfast.png") no-repeat;
}
.m-search-venues--venue-lists .services--hotel {
  background: url("./assets/icons/venue/services/white/hotel.png") no-repeat;
}
.m-search-venues--venue-lists .services--lunch {
  background: url("./assets/icons/venue/services/white/lunch.png") no-repeat;
