.taskName {
  border: none;
}
.my-staff-ss-item {
  height: 4em;
  border: 0.3px solid rgb(226, 226, 226);
  margin: 1em;
  margin-left: auto;
  margin-right: auto;
  position: relative;
  display: -ms-flexbox;
  display: flex;
}

.my-staff-ss-item a {
  position: absolute;
  top: -8px;
  right: -8px;
  background-color: #5d5bbc !important;
}

.icon-calendar {
  background: url("./img/calendar-icon.gif") no-repeat;
  height: 17px;
  width: 17px;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
}
.icon-breafcase {
  background: url("./img/breafcase-icon.gif") no-repeat;
  height: 17px;
  width: 17px;
  cursor: pointer;
  position: absolute;
  top: 35px;
  right: 10px;
}
.icon-time {
  background: url("./img/time-icon.gif") no-repeat;
  height: 17px;
  width: 17px;
  cursor: pointer;
  position: absolute;
  top: 60px;
  right: 10px;
}
.schedulepopover {
  background: rgba(255, 255, 255, 0.9);
  position: absolute;
  right: -358px;
  width: 325px;
  padding: 10px 20px;
  top: -5px;
  -webkit-box-shadow: #ccc 0px 0px 3px;
          box-shadow: #ccc 0px 0px 3px;
  border-radius: 10px;
  z-index: 9;
}
.schedulepopover:after,
.schedulepopover:before {
  right: 100%;
  top: 15%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.schedulepopover:after {
  border-color: rgba(136, 183, 213, 0);
  border-right-color: #fff;
  border-width: 17px;
  margin-top: -35px;
}
.schedulepopover:before {
  border-color: rgba(194, 225, 245, 0);
  border-right-color: #ddd;
  border-width: 18px;
  margin-top: -36px;
}
.schedulepopover .header {
  font-weight: normal;
  font-size: 14px;
  text-align: left;
  color: #5ec6e6;
}
.schedulepopover .body {
  font-size: 15px;
  color: #888;
  margin-top: 5px;
}
.schedulepopover .body table {
  width: 100%;
}
.schedulepopover .body td {
  padding: 5px 0;
  width: calc(100% / 3);
  text-align: left;
}
.schedulepopover .body td .time {
  background: #f1f1f1;
  font-size: 12px;
  text-align: center;
  padding: 5px 15px;
}
.schedulepopover .edit {
  float: right;
  font-size: 11px;
  text-transform: uppercase;
  position: relative;
  top: -6px;
  right: -7px;
  color: #5ec6e6;
  cursor: pointer;
}
.schedulepopover .edit .icon-edit {
  background: url("./img/edit-icon.gif") no-repeat;
  height: 17px;
  width: 17px;
  display: inline-block;
  top: 3px;
  right: -3px;
  position: relative;
}
.schedulepopover select {
  padding: 0 3px;
  width: auto;
  height: auto;
  border-radius: 2px;
  margin: 0;
  float: left;
  font-size: 10px !important;
}
.hide {
  display: none;
}

.payment-modal {
  width: 950px;
  height: auto;
  padding-bottom: 45px;
  padding-top: 35px;
  margin-bottom: 50px;
}
.payment-modal header {
  text-align: center;
  margin-bottom: 30px;
}
#btn-edit-working-hours {
  font-style: italic;
  color: #00b9d7;
  font-size: 14px;
  margin-bottom: 10px;
  cursor: pointer;
  display: inline-block;
}
#btn-edit-working-hours:hover {
  color: blue;
}
.payment-modal .payment-modal-content {
  display: -ms-flexbox;
  display: flex;
  font-size: 14px;
  color: #777;
}
.payment-modal .timesheet {
  -ms-flex: 2 1;
      flex: 2 1;
  border-right: 1px solid #ddd;
  margin-right: 20px;
}
.payment-modal table {
  border-collapse: collapse;
  width: 100%;
  text-align: center;
}

.payment-modal th,
.payment-modal td {
  padding: 8px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}
.payment-modal .summary {
  -ms-flex: 1 1;
      flex: 1 1;
}
.payment-modal .summary h3 {
  font-weight: 200;
  font-size: 20px;
}

.payment-modal .summary .pull-right {
