}
.payment-modal .summary .clearfix {
  padding: 15px 8px;
}
.payment-modal .summary .clearfix:hover {
  background: #fdfdfd;
}
.payment-modal .summary input[type="text"] {
  width: 50px;
  display: inline-block;
  text-align: center;
  padding: 10px;
  border: 1px solid #ccc;
}
.payment-modal .summary .grand-total {
  border-top: 1px solid #ccc;
  padding-top: 35px;
}
.payment-modal .summary .grand-total .pull-right {
  font-size: 20px;
  color: #674cbe;
}
.payment-modal .summary .btn-primary {
  margin-top: 50px;
}
.payment-modal .timesheet .time-container {
  background: white;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  text-align: center;
  cursor: pointer;
  height: 150px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  line-height: 2;
}
.payment-modal .timesheet .time-container:hover {
  background: #fafafa;
}
.payment-modal .timesheet .time-container * {
  text-align: center;
  font-size: 13px;
  cursor: pointer;
}



