.SubscribePopUp {
  background: #5a59a6;
  border-radius: 10px;
  height: 645px;
  width: 1095px;
  position: absolute;
  z-index: 9;
  color: #fff;
  font-weight: lighter;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  -webkit-animation: fadein 1s;
          animation: fadein 1s;
}
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.leftside {
  width: 450px;
  float: left;
  text-align: center;
  position: relative;
  left: 80px;
  top: 39px;
  letter-spacing: 1px;
}
.leftside h2 {
  margin-top: 10px;
  font-weight: lighter;
}
.leftside span {
  margin-top: 10px;
  letter-spacing: 1px;
}
.leftside img {
  margin-top: 30px;
}
.rightside {
  position: relative;
  top: 95px;
  width: 425px;
  right: 50px;
  float: right;
}
.leftside * {
  display: block;
  margin: auto;
}
.title {
}
span {
}

.rightside h3 {
  text-transform: uppercase;
}
.rightside h4 {
  margin-top: 60px;
  text-transform: uppercase;
}
.rightside ul {
  font-size: 18px;
  padding-left: 20px;
  margin-top: 40px;
  letter-spacing: 1px;
  line-height: 1.3;
}
.rightside ul li {
  margin-bottom: 10px;
  margin-top: 5px;
}
.rightside button {
  border: 1px solid #fff;
  width: auto;
  padding: 6px 18px;
  font-size: 17px;
  margin-right: 15px;
}
.btn-primary {
  background: #00d4e4;
  border-color: #00d4e4;
}

.subscribe-settings-modal .a-modal-content h5 {
  text-transform: uppercase;
  font-size: 20px;
  margin-top: 35px;
  margin-bottom: 25px;
  line-height: auto;
  font-weight: 400;
}
.subscribe-settings-modal .have-header h5 {
  margin-top: 15px;
  margin-bottom: 35px;
}
.subscribe-settings-modal .a-modal-content .step-2 {
  text-align: center;
}
.subscribe-settings-modal .a-modal-content .step-2 span {
  display: block;
  margin-top: 20px;
}

.subscribe-settings-modal .a-modal-content .step-2 p * {
  -webkit-transition: all 0.1s linear;
  -o-transition: all 0.1s linear;
  transition: all 0.1s linear;
}

.subscribe-settings-modal .a-modal-content .step-2 p img {
  width: 130px;
}

.subscribe-settings-modal .a-modal-content .step-2 p:hover {
  color: #43bfe9;
  cursor: pointer;
}

.subscribe-settings-modal .a-modal-content .step-2 p:hover img {
  width: 140px;
}
.subscribe-settings-modal .a-modal-content .step-2 p.selected img {
  background: #eee;
  padding: 10px;
  border-radius: 20px;
  border: 1px inset;
}
.subscribe-settings-modal .a-modal-content .step-2 .a-modal-footer {
  margin-top: 50px;
}

.subscribe-settings-modal .a-modal-content .step-3 .group {
  text-align: left;
  margin-bottom: 47px;
}

.subscribe-settings-modal .a-modal-content .step-3 .row {
  border-bottom: 1px solid #eee;
  padding: 5px 0;
  -ms-align-items: center;
  -ms-flex-align: center;
      align-items: center;
  margin: auto auto 5px;
}

.subscribe-settings-modal .a-modal-content .step-3 .row .col-md-2 {
  padding: 0 18px 0 0;
  font-size: 12px;
}

.subscribe-settings-modal .a-modal-content .step-3 .row .col-md-6 span {
  margin-right: 10px;
  font-size: 16px;
}

.subscribe-settings-modal .a-modal-content .step-3 .row .col-md-5 {
  padding-left: 0;
}

.subscribe-settings-modal .a-modal-content .step-3 .row .col-md-5 span {
  font-size: 13px;
}

.subscribe-settings-modal .a-modal-content .step-3 .row .fa-minus-circle {
  color: #5e5cbd;
  font-size: 20px;
}

.subscribe-settings-modal .a-modal-content .step-3 .primary {
  color: #bbb;
}

.subscribe-settings-modal .a-modal-content .step-3 img {
  width: 100%;
  margin: auto;
}

.subscribe-settings-modal .a-modal-content .step-3 .btn-delete:hover {
  opacity: 0.8;
  cursor: pointer;
}



